<!-- 
    @desc Ëá™ÂÆö‰πâNavbarÁªÑ‰ª∂
 -->

<template>
  <view class="container-navbar">
    <image
      class="container-top-bg"
      :src="imgSrc"
      mode="widthFix"
      lazy-load="false"
      binderror=""
      bindload=""
      :style="{ height: imgHeight }"
    ></image>

    <!-- Ëá™ÂÆö‰πâÈ°∂ÈÉ®Â∑¶‰æßËÉ∂Âõä -->
    <view
      class="container-capsule"
      :style="{ paddingTop: menuInfo.statusBarHeight }"
    >
      <view
        class="menu_btn"
        v-if="isShowBack"
        :style="{
          position: 'fixed',
          top: menuInfo.menuTop,
          left: menuInfo.menuRight,
          width: menuInfo.menuWidth,
          height: menuInfo.menuHeight
        }"
      >
        <u-icon
          name="arrow-left"
          class="arrowleft"
          color="#fff"
          :size="size"
        ></u-icon>
      </view>

      <slot name="other"></slot>
    </view>
  </view>
</template>

<script>
export default {
  name: 'soumnsNavbar',
  props: {
    imgSrc: {
      type: String,
      required: true
    }, // ÂõæÁâáË∑ØÂæÑ
    imgHeight: {
      type: String,
      required: true
    }, // ÂõæÁâáÈ´òÂ∫¶
    isShowBack: {
      type: Boolean,
      required: true,
      default: true
    }, // ÊòØÂê¶Â±ïÁ§∫ËøîÂõûÊåâÈíÆ
    size: {
      type: String,
      required: true,
      default: '24'
    } // Â∑¶‰æßÊåâÈíÆÂ∞∫ÂØ∏
  },
  data() {
    return {
      menuInfo: {
        statusBarHeight: '', // ÊâãÊú∫Á≥ªÁªüÁöÑÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
        menuTop: '', // Âè≥‰æßÁöÑËÉ∂ÂõäÈ°∂ÈÉ®Ë∑ùÁ¶ªÂ±èÂπïÈ°∂ÈÉ®ÁöÑË∑ùÁ¶ª--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
        menuRight: '', // Âè≥‰æßÁöÑËÉ∂ÂõäË∑ùÁ¶ªÂè≥‰æßÂ±èÂπïË∑ùÁ¶ª--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
        menuWidth: '', // Âè≥‰æßÁöÑËÉ∂ÂõäÂÆΩÂ∫¶--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
        menuHeight: '' // Âè≥‰æßÁöÑËÉ∂ÂõäÈ´òÂ∫¶--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
      }
    };
  },

  mounted() {
    this.initNavBar();
  },

  methods: {
    initNavBar() {
      if (
        !Object.keys(uni.getStorageSync('menuInfo')).length ||
        !uni.getStorageSync('menuInfo')
      ) {
        uni.getSystemInfo({
          success: result => {
            // Ëé∑ÂèñÊâãÊú∫Á≥ªÁªüÁöÑÁä∂ÊÄÅÊ†èÈ´òÂ∫¶Ôºà‰∏çÂêåÊâãÊú∫ÁöÑÁä∂ÊÄÅÊ†èÈ´òÂ∫¶‰∏çÂêåÔºâ  Ôºà ‰∏çË¶Å‰ΩøÁî®uni-appÂÆòÊñπÊñáÊ°£ÁöÑvar(--status-bar-height) ÂÆòÊñπËøô‰∏™ÊòØÂõ∫ÂÆöÁöÑ20px  ‰∏çÂØπÁöÑ Ôºâ
            let statusBarHeight = result.statusBarHeight + 'px';
            // Ëé∑ÂèñÂè≥‰æßËÉ∂ÂõäÁöÑ‰ø°ÊÅØ Âçï‰Ωçpx
            const menuButtonInfo = uni.getMenuButtonBoundingClientRect();

            // bottom: ËÉ∂ÂõäÂ∫ïÈÉ®Ë∑ùÁ¶ªÂ±èÂπïÈ°∂ÈÉ®ÁöÑË∑ùÁ¶ª
            // height: ËÉ∂ÂõäÈ´òÂ∫¶
            // left:   ËÉ∂ÂõäÂ∑¶‰æßË∑ùÁ¶ªÂ±èÂπïÂ∑¶‰æßÁöÑË∑ùÁ¶ª
            // right:  ËÉ∂ÂõäÂè≥‰æßË∑ùÁ¶ªÂ±èÂπïÂ∑¶‰æßÁöÑË∑ùÁ¶ª
            // top:    ËÉ∂ÂõäÈ°∂ÈÉ®Ë∑ùÁ¶ªÂ±èÂπïÈ°∂ÈÉ®ÁöÑË∑ùÁ¶ª
            // width:  ËÉ∂ÂõäÂÆΩÂ∫¶
            // console.log('ËÆ°ÁÆóËÉ∂ÂõäÂè≥‰æßË∑ùÁ¶ªÂ±èÂπïÂè≥ËæπË∑ùÁ¶ª', result.screenWidth - menuButtonInfo.right)
            let menuWidth = menuButtonInfo.width + 'px';
            let menuHeight = menuButtonInfo.height + 'px';
            let menuBorderRadius = menuButtonInfo.height / 2 + 'px';
            let menuRight =
              result.screenWidth - menuButtonInfo.right + 10 + 'px';
            let menuTop = menuButtonInfo.top + 'px';
            let contentTop = result.statusBarHeight + 44 + 'px';
            let menuInfo = {
              statusBarHeight: statusBarHeight, // Áä∂ÊÄÅÊ†èÈ´òÂ∫¶----Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÈ°∂ÈÉ®ÂØºËà™Êù°ËÆæËÆ°padding-top‰ΩøÁî®ÔºöÁõÆÁöÑÁïôÂá∫Á≥ªÁªüÁöÑÁä∂ÊÄÅÊ†èÂå∫Âüü
              menuWidth: menuWidth, // Âè≥‰æßÁöÑËÉ∂ÂõäÂÆΩÂ∫¶--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
              menuHeight: menuHeight, // Âè≥‰æßÁöÑËÉ∂ÂõäÈ´òÂ∫¶--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
              menuBorderRadius: menuBorderRadius, // ‰∏ÄÂçäÁöÑÂúÜËßí--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
              menuRight: menuRight, // Âè≥‰æßÁöÑËÉ∂ÂõäË∑ùÁ¶ªÂè≥‰æßÂ±èÂπïË∑ùÁ¶ª--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
              menuTop: menuTop, // Âè≥‰æßÁöÑËÉ∂ÂõäÈ°∂ÈÉ®Ë∑ùÁ¶ªÂ±èÂπïÈ°∂ÈÉ®ÁöÑË∑ùÁ¶ª--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂ∑¶‰æßËÉ∂ÂõäËÆæÁΩÆ‰ΩøÁî®
              contentTop: contentTop // ÂÜÖÂÆπÂå∫Ë∑ùÁ¶ªÈ°µÈù¢ÊúÄ‰∏äÊñπÁöÑÈ´òÂ∫¶--Áî®Êù•ÁªôËá™ÂÆö‰πâÂØºËà™Êù°È°µÈù¢ÁöÑÂÜÖÂÆπÂå∫ÂÆö‰ΩçË∑ùÁ¶ª‰ΩøÁî®
            };
            console.log(menuInfo, 'üíôüíõ menuInfo');
            uni.setStorageSync('menuInfo', menuInfo);
          },
          fail: error => {
            console.log(error);
          }
        });
      } else {
        this.menuInfo = uni.getStorageSync('menuInfo');
      }

      console.log('üíõüíô menuInfo:', this.menuInfo);
    }
  }
};
</script>

<style scoped lang="scss">
