    font-weight: 500;

    .menu_btn {
      overflow: hidden;
      display: flex;
      align-items: center;
      position: fixed; // Ë°åÂÜÖÂºèÂÜô‰∫ÜÂõ∫ÂÆöÂÆö‰Ωç--ÁõÆÁöÑÊòØÂéªÊéâ‰∏ãÂàíÈ°µÈù¢‰∏ÄËµ∑ÊªöÂä®ÈóÆÈ¢ò
